"use strict";
import { BaseButton } from "components/Button/buttons";
import { useIsMobile } from "hooks/screenSize/useIsMobile";
import styled from "lib/styled-components";
import { BelowFloorWarningModal } from "nft/components/profile/list/Modal/BelowFloorWarningModal";
import { findListingIssues } from "nft/components/profile/list/utils";
import { useSellAsset } from "nft/hooks";
import { useMemo, useState } from "react";
import { BREAKPOINTS } from "theme";
import { Plural, Trans, t } from "uniswap/src/i18n";
const StyledListingButton = styled(BaseButton)`
  background: ${({ showResolveIssues, theme }) => showResolveIssues ? theme.critical : theme.accent1};
  color: ${({ theme }) => theme.deprecated_accentTextLightPrimary};
  font-weight: 535;
  font-size: 20px;
  line-height: 24px;
  padding: 16px;
  border-radius: 12px;
  width: min-content;
  border: none;
  cursor: ${({ missingPrices }) => missingPrices ? "auto" : "pointer"};
  opacity: ${({ showResolveIssues, missingPrices }) => !showResolveIssues && missingPrices && "0.3"};

  @media screen and (max-width: ${BREAKPOINTS.sm}px) {
    font-size: 16px;
    line-height: 20px;
    padding: 10px 12px;
  }
`;
export const ListingButton = ({ onClick }) => {
  const { sellAssets, showResolveIssues, toggleShowResolveIssues, issues, setIssues } = useSellAsset(
    ({ sellAssets: sellAssets2, showResolveIssues: showResolveIssues2, toggleShowResolveIssues: toggleShowResolveIssues2, issues: issues2, setIssues: setIssues2 }) => ({
      sellAssets: sellAssets2,
      showResolveIssues: showResolveIssues2,
      toggleShowResolveIssues: toggleShowResolveIssues2,
      issues: issues2,
      setIssues: setIssues2
    })
  );
  const [showWarning, setShowWarning] = useState(false);
  const isMobile = useIsMobile();
  const [listingsMissingPrice, listingsBelowFloor] = useMemo(() => {
    const {
      missingExpiration,
      overMaxExpiration,
      listingsMissingPrice: listingsMissingPrice2,
      listingsBelowFloor: listingsBelowFloor2,
      listingsAboveSellOrderFloor
    } = findListingIssues(sellAssets);
    const foundIssues = Number(missingExpiration) + Number(overMaxExpiration) + listingsMissingPrice2.length + listingsAboveSellOrderFloor.length;
    setIssues(foundIssues);
    !foundIssues && showResolveIssues && toggleShowResolveIssues();
    if ((missingExpiration || overMaxExpiration || listingsAboveSellOrderFloor.length) && !showResolveIssues) {
      toggleShowResolveIssues();
    }
    return [listingsMissingPrice2, listingsBelowFloor2];
  }, [sellAssets, setIssues, showResolveIssues, toggleShowResolveIssues]);
  const warningWrappedClick = () => {
    if (issues) {
      !showResolveIssues && toggleShowResolveIssues();
    } else if (listingsBelowFloor.length) {
      setShowWarning(true);
    } else {
      onClick();
    }
  };
  return <>
    <StyledListingButton
      onClick={warningWrappedClick}
      missingPrices={!!listingsMissingPrice.length}
      showResolveIssues={showResolveIssues}
    >{showResolveIssues ? <Plural
      value={issues !== 1 ? 2 : 1}
      one={t("common.resolveIssue")}
      other={t("common.resolveIssues", { issues })}
    /> : listingsMissingPrice.length && !isMobile ? <Trans i18nKey="nft.setPrices" /> : <Trans i18nKey="nft.startListing" />}</StyledListingButton>
    {showWarning && <BelowFloorWarningModal
      listingsBelowFloor={listingsBelowFloor}
      closeModal={() => setShowWarning(false)}
      startListing={onClick}
    />}
  </>;
};
